<template>
    <div class="user-info">
        <div class="info" v-if="edit">
            <div class="user-table">
                <table width="1200" height="400">
                    <tr>
                        <td rowspan="4" width="50px">头像:</td>
                        <td rowspan="4" width="210px">
                            <el-upload
                                                    class="avatar-uploader"
                                                    :show-file-list="false"
                                                    :on-success="handleAvatarSuccess"
                                                    :before-upload="beforeAvatarUpload"
                                                    action="/api/upload/img"
                                                    >
                                                <img v-if="squareUrl" :src="squareUrl" class="avatar">
                                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </td>
                        <td width="60px" height="140px" rowspan="2">用户名: </td>
                        <td width="250px" rowspan="2">
                            <el-input v-model="userForm.username" class="create-input" placeholder="请输入内容" @blur="validateUsername"></el-input>
                        </td>
                        <td rowspan="4" width="50px">简介:</td>
                        <td rowspan="4" width="350px">
                            <el-input  v-model="userForm.sign"  type="textarea" class="create-input" placeholder="请输入内容"></el-input>
                        </td>
                        <td height="10px" align="right" width="240px">
                            <el-button
                                    size="medium"
                                     @click="edit=!edit"
                             >返回</el-button>
                            <el-button
                                    class="send"
                                    type="primary"
                                    size="medium"
                                    @click="save"
                            >保存</el-button>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                    </tr>
                    <tr>
                        <td rowspan="2" width="60px">手机号:</td>
                        <td rowspan="2" width="250px">
                            <el-input v-model="userForm.mobile" class="create-input" placeholder="请输入内容" @blur="validateMobile"></el-input>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                    </tr>
                </table>
            </div>
        </div>
        <div class="info" v-if="!edit">
            <div class="user-table">
                <table width="1200" height="400">
                    <tr>
                        <td rowspan="4" width="50px">头像:</td>
                        <td rowspan="4" width="210px">
                            <el-avatar shape="square" :size="200" :src="squareUrl"></el-avatar>
                        </td>
                        <td width="60px" height="80px" rowspan="2">用户名: </td>
                        <td width="250px" rowspan="2">
                            <p>{{ userForm.username }}</p>
                        </td>
                        <td rowspan="4" width="50px">简介:</td>
                        <td rowspan="4" width="350px">
                            <p>{{ userForm.sign }}</p>
                        </td>
                        <td height="10px" align="right" width="240px">
                            <el-button
                                    size="medium"
                                    @click="edit=!edit"
                            >编辑</el-button>
                            <el-button
                                    class="send"
                                    type="primary"
                                    size="medium"
                                    @click="save"
                            >保存</el-button>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                    </tr>
                    <tr>
                        <td rowspan="2" width="60px">手机号:</td>
                        <td rowspan="2" width="250px">
                            <p>{{ userForm.mobile }}</p>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                    </tr>
                </table>
            </div>
        </div>
    </div>

</template>

<script>
    import {mapGetters} from "vuex";
    import { megWarn, megSuc, megError } from '@/utils/message'
    import { getUserInfo, updateUserInfo } from "@/service/user-api"
    import {getCookie} from "../../utils/cookies";

    export default {
        name: "UserInfo",
        computed: {
            ...mapGetters([
                "consumerInfo"
            ]),
        },
        created() {
            getUserInfo(this.userId)
            .then((res) => {
                this.userForm = res.user_info
                this.squareUrl = res.user_info.avatar
            })
        },
        data(){
            return {
                edit: false,
                userId: JSON.parse(getCookie('userInfo')).id,
                squareUrl: "https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png",
                userForm: ''
            }
        },
        methods: {
            handleAvatarSuccess(res, file) {
                if (res.code === 2000){
                    this.squareUrl = URL.createObjectURL(file.raw);
                    this.userForm.avatar = res.url
                } else {
                    megError('上传图片失败!')
                }


            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            save(){
                updateUserInfo(this.userId, this.userForm)
                    .then(() => {
                        this.$store.dispatch('setUserInfo', this.userForm)
                            .then(() => {
                                megSuc('修改成功!')
                                this.edit = false
                            })
                    })
            },
            validateMobile(){
                let re = /^1[3-9]\d{9}$/
                if (this.userForm.mobile === ''){
                    megWarn('手机号必填')
                } else if (!re.test(this.userForm.mobile)){
                    megWarn('手机号格式错误')
                }
            },
            validateUsername(){
                if (this.userForm.username === ''){
                    megWarn('用户名必填')
                } else if (this.userForm.username < 3 || this.userForm.username > 20){
                    megWarn('用户名为3-20位')
                }
            }
        }
    }
</script>

<style lang="scss">
    .info{
      padding-top: -100px;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        .user-table{
            background-color: #fff;
            width: 1200px;
            height: 400px;
        }
    };
    .user-info{
        padding-top: 60px;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        .edit{
            background-color: #fff;
            width: 1200px;
            height: 400px;
            padding: 10px 0 20px 20px;
            flex-direction: row;
            display: flex;
            .edit-avatar{
                display: flex;
                margin-bottom: 20px;
                align-items: center;
            };
            .edit-item{
                display: flex;
                margin: 40px 40px;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                span{
                    margin: 40px;
                }
            }
            .edit-button{
                margin-left: 300px;
            }
            .label{
                margin-right: 10px
            }

        }
    }

    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 200px;
        height: 200px;
        line-height: 200px;
        text-align: center;
    }
    .avatar {
        width: 200px;
        height: 200px;
        display: block;
    }

</style>
